.exhibitions-content {
    width: 100vw;
    height: calc(100vh - 112px - 2.93vw - 18.5px);
    margin-top: 0;
    overflow:auto;
}


.exhibitions-footerList {
    width: 100vw;
    height: calc(112px + 2.93vw);
    position: absolute;
    bottom: 0;
}


.line {
  margin: 0;
    width: 100vw;
    height: 2.93vw;
    background: url("/static/museum/line.png") no-repeat  center / 100% 100% ;
}

.footer-list {
    margin-top: 12px;
    width: 100vw;
    height: 85px;
    display: -ms-flexbox;
    display: flex;
    overflow:auto;
}

.exhibitions-list-item {
    width: 85px;
    height: 85px;
    margin-left: 15px;
}


.exhibitions-footerList  .list-view-section-body {
  background-color: #f5f5f9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.exhibitions-content-cover {
  padding: 15px 15px 22px;
  height: auto;
  text-align: center;
}

.exhibitions-content-title {
  margin: 0 15px;
  text-align: center;
  color: #666666;
  font-size: 0.95rem;
}

.exhibitions-content-digest {
  margin: 19px 15px;
  text-align: left;
  color: #333333;
  font-size: 0.85rem;
}

.back-button {
  position: absolute;
  width: 59.5px;
  height: 91px;
  right: 0.5px;
  top: -2px;
  background: url("/static/museum/back.png") no-repeat  center / 100% 100% ;

}



