.event-cell {
    width: 100vw;
    height: calc(100vh - 45px);
    background-color: #eeeeee;
    margin-top: 45px;
}


html:not([data-scale]) .am-list-body::before {
  height: 0;
}

html:not([data-scale]) .am-list-body::after {
  height: 0;
}


.event-cell .am-list-header {
  width: 100vw;
  height: 9.5px;
  margin: 0;
  padding: 0;
}

.event-cell .am-list-footer {
  margin: 0;
  padding: 0;
}
.event-card {
  background-color: white;
  margin: 0 15px 14.5px;
  width: calc(100vw - 30px);
  border-radius: 2px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.event-title {
    padding: 14.5px 12px 0 12px;
    max-height: 49.5px;
    line-height: 25px;
    font-size: 1.1rem;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    line-clamp: 2;
    font-weight: bold;
    margin-bottom: 12.5px;
}

.event-cover-wrapper {
    margin: 12.5px 10px 0;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    height: 180px;
    border-radius: 2px;
}

.left-mark {
   width: 64.5px;
   height: 100%;
   border-radius: 2px;
   border-width: 0.95px;
   border-color: #cfcfcf;
   border-style: solid;
   text-align: center;
   line-height: 23.5px;
   color: #999999;
   float: left;
   font-size: 12px;
}

.event-time {
  height: 23.5px;
  padding: 10px 9.5px 0;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.event-time-content {
  height: 100%;
  padding-left: 9px;
  float: left;
  color: #666666;
  line-height: 25px;
  font-size: 14px;
}

.event-place {
  height: 23.5px;
  padding: 6px 9.5px 11px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.event-place-content {
  height: 100%;
  padding-left: 9px;
  float: left;
  color: #666666;
  line-height: 25px;
  font-size: 14px;
}


.event-footer {
  width: 100%;
  height: 55.5px;
  background-color: #eeeeee;
}

.footer-image {
  background-image: `url('/static/museum/tail.png');
  width: 100%;
  height: 100%;
}

.event-cell .list-view-section-body {
  background-color: #eeeeee;
}

.event-cell .am-list-body {
  min-height: calc(100vh - 45px - 9.5px - 85.5px);
  background: #eeeeee;
}



