.Home {
  width: 100vw;
}

.Home .am-tabs-default-bar-content {
    height: 45px;
    background: url("/static/museum/headbg.png") no-repeat  center / 100vw 45px;
}

.Home .am-tabs-default-bar-tab{
    color: rgba(255, 255, 255, 0.49);
    font-size: 16px;
}

.Home .am-tabs-default-bar-tab-active{
    color: white;
    font-size: 17px;
}

.Home .am-tabs-default-bar-underline{
    border:0;
    height: 3px;
    border-radius: 1.5px;
    background-color: #E36161;
    width: 23% !important;
    margin-left: 5%;
}

html:not([data-scale]) .am-tabs-default-bar-top .am-tabs-default-bar-tab::after{
    height: 0px;
}



