.hall-cell {
    width: 100vw;
    min-height: calc(100vh - 45px);
    background-color: #eeeeee;
    margin-top: 45px;
    padding-top: 9.5px;
}

.hall-cell-list {
  min-height: calc(100vh - 100.5px);

}

.hall_section {
    width: 100vw;
    height: 16.5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.hall_section_square {
  width: 5.5px;
  height: 16.5px;
  background-color: #5E1313;
  margin-left: 15px;
}

.hall_section_title {
  padding-left: 9px;
  height: 16.5px;
  line-height: 16.5px;
  color: #5E1313;
  font-size: 0.9rem;

}

.hall-card {
  background-color: white;
  margin: 8.5px 15px 14.5px;
  width: calc(100vw - 30px);
  border-radius: 2px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.hall-title {
    padding: 14.5px 12px 0 12px;
    max-height: 49.5px;
    line-height: 25px;
    font-size: 1.1rem;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    line-clamp: 2;
    font-weight: bold;
    margin-bottom: 12.5px;
}

.hall-cover-wrapper {
    margin: 12.5px 10px 0;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    height: 180px;
    border-radius: 2px;
}

.left-mark {
   width: 64.5px;
   height: 100%;
   border-radius: 2px;
   border-width: 0.95px;
   border-color: #cfcfcf;
   border-style: solid;
   text-align: center;
   line-height: 23.5px;
   color: #999999;
   float: left;
   font-size: 12px;
}

.hall-place {
  height: 23.5px;
  padding: 11.5px 9.5px 11px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}

.hall-place-content {
  height: 100%;
  padding-left: 9px;
  float: left;
  color: #666666;
  line-height: 25px;
  font-size: 14px;
}


.hall-footer {
  width: 100%;
  height: 55.5px;
  background-color: #eeeeee;
}

.footer-image {
  background-image: `url('/static/museum/tail.png');
  width: 100%;
  height: 100%;
}



.hall-cell .list-view-section-body {
  background-color: #eeeeee;
}



