.event-content-root {
  width: 100vw;
  min-height: calc(100vh - 55.6px);
}


.content-headcover {
  margin: 0;
  width: 100vw;
  height: calc(100vw / 1.52);
}


.content-title {
  margin: 25.5px 16px 0;
  line-height: 20px;
  font-weight: bold;
  font-size: 1.1rem;
  text-align: center;
}


.info-event-bg {
  width: 100vw;
  height: calc(100vw * 0.29);
  margin-top: 27.5px;
  position: relative;
  background: url("/static/museum/zhanlan_bg.png") no-repeat  center / 100vw 29vw ;

}

.info-hall-bg {
  width: 100vw;
  height: calc(100vw * 0.24);
  margin-top: 27.5px;
  position: relative;
  background: url("/static/museum/zhanlan_bg2.png") no-repeat  center / 100vw 24vw ;

}


.info-top {
    position: absolute;
    left: 30.5px;
    right: 0;
    height: 27px;
    top: calc(100vw * 0.055);
}



.info-bottom {
    position: absolute;
    left: 30.5px;
    right: 0;
    height: 27px;
    bottom: calc(100vw * 0.055);

}

.left-box {
    width: 64.5px;
    height: 100%;
    border-radius: 2px;
    border-width: 0.95px;
    border-color: #cfcfcf;
    border-style: solid;
    text-align: center;
    line-height: 27px;
    color: #999999;
    float: left;
    font-size: 0.8rem;
}

.info-title {
    height: 100%;
    padding-left: 9px;
    float: left;
    color: #666666;
    line-height: 27px;
    font-size: 0.85rem;
}

.hall-info-title {
    position: absolute;
    left: calc(100vw * 0.38);
    right: 0;
    top: calc(100vw * 0.063);
    height: calc(100vw * 0.072);
    color: #666666;
    line-height: calc(100vw * 0.072);
    font-size: 0.88rem;
}


.line {
    margin-top: 26px;
    width: 100vw;
    height: 2.93vw;
    background: url("/static/museum/line.png") no-repeat  center / 100% 100% ;
}

.content {
    margin: 28px 16px 0px;
    line-height: 24px;
    font-size: 0.8rem;
    color: #333333;
}

.footer-image {
    width: 100%;
    height: 55.5px;
    background-color: #eeeeee;
    background: url("/static/museum/tail.png") no-repeat  center / 100vw ;
}

.exhibition_button {
  position: absolute;
  width: 50.5px;
  height: 109px;
  right: 10px;
  top: -2px;
  background: url("/static/museum/zhanpin.png") no-repeat  center / 100% 100% ;

}

.virtual-exhibit {
  position: absolute;
  width: 79px;
  height: 30.5px;
  top: calc(100vw / 1.52 - 30.5px);
  left: 0;
  background: url("/static/museum/xuniyoulan.png") no-repeat  center / 100% 100% ;

}



